<template>
  <div id="leftMenuList2" v-show="showList" :style="{height:maxHeight}">
    <div class="background"></div>
    <ul class="itemsList" @mouseover="entered2()" @mouseleave="iamout2()">
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(1)">阿里云计算</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(2)">产品</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(3)">解决方案</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(4)">云市场</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(5)">开发者</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(6)">支持与服务</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(7)">云栖社区</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(8)">阿里云大学</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(8)">{{showS[0]}}{{showS[1]}}{{maxHeight}}</div>
        </a>
      </li>
    </ul>
    <div
      class="thirdMenu"
      :class="{'thirdMenuOut':!showS[0], 'thirdMenuEnter':showS[0]}"
      :style="{height:maxHeight}"
    >
      <item1s2 :show1="showS[1]"></item1s2>
    </div>
  </div>
</template>

<script>
import item1s2 from "./item1s2";
export default {
  name: "leftMenuList2",
  props: {
    showList: Boolean,
    required: true
  },
  data() {
    return {
      showS: [false, false, false, false, false, false, false, false, false],
      showAnimation: true,
      maxHeight: document.body.clientHeight - 45 + "px"
    };
  },
  components: { item1s2 },
  methods: {
    entered2: function() {
      this.showS[0] = true;
    },
    iamout2: function() {
      this.showS[0] = false;
    },
    showItem: function(index) {
      showS: [true, false, false, false, false, false, false, false, false];
      this.showS[index] = true;
    }
  },
  computed: {}
};
</script>

<style scoped>
#leftMenuList2 {
  min-height: 640px;
  position: absolute;
  top: 0px;
  width: 200px;
}
.background {
  width: 100%;
  height: 100%;
  background-color: rgb(39, 42, 45);
  position: absolute;
  z-index: -1;
}
.itemsList {
  height: 100%;
  width: 100%;
  list-style: none;
  text-align: left;
}
.item {
  height: 40px;
  width: 200px;
  padding-left: 18px;
  padding-top: 5px;
}
li a:hover {
  transition: 0.3s;
  color: #66cae2;
}
a:link,
a:visited {
  font: Tahoma, Helvetica, Arial, "宋体", sans-serif;
  font-weight: 300;
  font-size: 15px;
  transition: 0.3s;
  color: rgb(235, 235, 235);
  text-decoration: none;
}
.thirdMenu {
  width: 700px;
  background-color: rgb(48, 53, 55);
  position: absolute;
  z-index: -100;
  top: 0px;
  transition: 0.3s;
}
.animation {
  transition: 0.5s;
}
.thirdMenuEnter {
  left: 201px;
}
.thirdMenuOut {
  left: -1000px;
}
</style>


