<template>
  <div id="leftMenuList" :class="{'hidden':!onMenu, 'showList':onMenu}" :style="{height:maxHeight}">
    <div class="background"></div>
    <ul class="itemsList" @mouseover="entered()" @mouseleave="iamout()">
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(1),regualrWidth()">阿里云计算</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="specialWidth(),showItem(2)">产品</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(3),regualrWidth()">解决方案</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(4),regualrWidth()">云市场</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(5),regualrWidth()">开发者</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(6),regualrWidth()">支持与服务</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(7),regualrWidth()">云栖社区</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(8),regualrWidth()">阿里云大学</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item" @mouseover="showItem(9),regualrWidth()">合作与生态{{showAnimation}}</div>
        </a>
      </li>
    </ul>
    <hr>
    <div
      class="secondMenu"
      @mouseover="enteredS"
      @mouseout="iamout"
      :class="{'secondMenuOut':!show[0], 'secondMenuEnter':show[0],'animation':showAnimation,'specialBorder':sBorder}"
      :style="{height:maxHeight,width:secondMenuWidth}"
    >
      <item1List :show1="show[1]"></item1List>
      <leftMenuList2 :showList="show[2]"></leftMenuList2>
      <item3List :show3="show[3]"></item3List>
    </div>
    <ul class="itemsList">
      <li>
        <a href="#">
          <div class="item">政府通道</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item">企业孵化</div>
        </a>
      </li>
      <li>
        <a href="#">
          <div class="item">联系我们</div>
        </a>
      </li>
    </ul>
    <span class="contact">售前咨询 95187转1</span>
  </div>
</template>

<script>
import item1List from "./item1";
import item3List from "./item3";
import leftMenuList2 from "./LeftMenuList2";
export default {
  name: "leftMenuList",
  props: {
    onMenu: Boolean,
    required: true
  },
  data() {
    return {
      show: [
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false,
        false
      ],
      showAnimation: true,
      maxHeight: document.body.clientHeight - 45 + "px",
      secondMenuWidth: "1000px",
      sBorder: false
    };
  },
  components: { item1List, item3List, leftMenuList2 },
  methods: {
    entered: function() {
      this.show[0] = true;
      setTimeout(() => {
        this.showAnimation = false;
      }, 300);
    },
    enteredS: function() {
      this.showAnimation = false;
      this.show[0] = true;
    },
    iamout: function() {
      if (!this.show[2]) {
        this.showAnimation = true;
      }
      this.show[0] = false;
    },
    specialWidth: function() {
      this.secondMenuWidth = "200px";
      this.sBorder = true;
    },
    regualrWidth: function() {
      this.secondMenuWidth = "1000px";
      this.sBorder = false;
    },
    showItem: function(index) {
      for (var i = 1; i < 10; i++) {
        this.show[i] = false;
      }
      this.show[0] = true;
      this.show[index] = true;
    }
  },
  computed: {}
};
</script>

<style scoped>
#leftMenuList {
  min-height: 640px;
  position: absolute;
  top: 45px;
  width: 200px;
  transition: 0.3s;
  z-index: 9999;
}
.background {
  width: 100%;
  height: 100%;
  background-color: rgb(39, 42, 45);
  border-right: 1px solid rgb(64, 69, 74);
  position: absolute;
  z-index: -2;
}
.hidden {
  left: -250px;
}
.showList {
  left: 0px;
}
.itemsList {
  list-style: none;
  text-align: left;
}
.item {
  height: 40px;
  padding-left: 18px;
  padding-top: 5px;
}
.item:hover {
  transition: 0.5s;
  color: #66cae2;
}
a:link,
a:visited {
  font: Tahoma, Helvetica, Arial, "宋体", sans-serif;
  font-weight: 300;
  font-size: 15px;
  transition: 0.3s;
  color: rgb(235, 235, 235);
  text-decoration: none;
}
.secondMenu {
  background-color: rgb(48, 53, 55);
  position: absolute;
  top: 0px;
  z-index: -9999;
}
.animation {
  transition: 0.5s;
}
.specialBorder {
  border-right: 1px solid rgb(100, 100, 100);
}
.secondMenuEnter {
  left: 200px;
}
.secondMenuOut {
  left: -1300px;
}
hr {
  border: none;
  outline: none;
  height: 1px;
  background-color: rgb(100, 100, 100);
  width: 83%;
}
.contact {
  bottom: 30px;
  width: 90%;
  height: 20px;
  text-align: center;
  position: absolute;
  font-size: 10px;
  font-weight: 300;
  left: 0px;
  color: white;
}
</style>


