<template>
  <div id="upperHeaderUpper">
    <div class="leftMenu" @mouseover="iamonMenu" @mouseout="iamNotonMenu">
      <img class="leftmenulogo" src="./images/leftmenulogo.png" alt="M">
      <leftMenuList :onMenu="onMenu"></leftMenuList>
    </div>
    <div class="logo">
      <img class="logo" src="./images/alilogo.png" alt="aliLogo">
    </div>
    <div class="rightNav">
      <ul class="navList">
        <li>
          <a href="#">
            <img class="shoppingcart" src="./images/shoppingcart.png" alt="shoppingcart"> 购物车
          </a>
        </li>
        <li>
          <a href="#">控制台</a>
        </li>
        <li>
          <a href="#">文档</a>
        </li>
        <li>
          <a href="#">备案</a>
        </li>
        <li>
          <a href="#">邮箱</a>
        </li>
        <li>
          <a href="#">登录</a>
        </li>
      </ul>
      <div class="Clist" @mouseover="iamonCL" @mouseout="iamNotonCL">
        <a href="#">中国站</a>
        <countryList :onCL="onCL"></countryList>
      </div>
      <div class="searchBar">
        <input class="input" type="text" placeholder="搜索">
        <a href="#">
          <div class="scopeButton"></div>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
import leftMenuList from "./leftMenuList";
import countryList from "./countryList";
export default {
  name: "upperHeaderUpper",
  data() {
    return {
      onMenu: false,
      onCL: false
    };
  },
  components: { leftMenuList, countryList },
  methods: {
    iamonMenu: function() {
      this.onMenu = true;
    },
    iamNotonMenu: function() {
      this.onMenu = false;
    },
    iamonCL: function() {
      this.onCL = true;
    },
    iamNotonCL: function() {
      this.onCL = false;
    }
  }
};
</script>

<style scoped>
#upperHeaderUpper {
  background-color: none;
  border-bottom: 1px solid #292a2e;
  height: 45px;
  min-width: 1000px;
}
.leftMenu {
  width: 45px;
  height: 45px;
  position: absolute;
  left: 0px;
  color: rgb(235, 235, 235);
  text-align: center;
  line-height: 45px;
  border-right: 1px solid #292a2e;
  transition-duration: 1s;
}
.leftMenu:hover {
  transition-delay: 0.1s;
  transition-duration: 0.5s;
  background-color: #66cae2;
}
.leftmenulogo {
  height: 100%;
  width: auto;
}
.logo {
  margin-left: 6px;
  height: 44px;
  width: auto;
  position: absolute;
  left: 20px;
}
ul {
  float: right;
}
li {
  display: inline;
  padding-right: 25px;
  line-height: 45px;
}
.searchBar {
  float: right;
  position: relative;
}
.Clist {
  float: right;
  line-height: 45px;
  width: 100px;
  text-align: center;
}
.scopeButton {
  background: url("./images/scopebutton.png") no-repeat center center;
  background-size: 100%;
  width: 20px;
  height: 20px;
  border: none;
  position: absolute;
  right: 0px;
  margin: 12px 10px;
}
.input {
  width: 200px;
  height: 46px;
  border: none;
  outline: none;
  background-color: rgba(51, 55, 57, 0.5);
  transition-duration: 1s;
  color: rgb(235, 235, 235);
  font-size: 15px;
  padding-left: 10px;
  transition: 0.8s;
  position: absolute;
  right: 0px;
}
.input:hover {
  height: 44px;
  width: 300px;
  transition-duration: 0.5s;
  border: 1px solid #66cae2;
  outline: none;
}
.input:focus {
  height: 44px;
  outline: none;
  border: 1px solid #66cae2;
  width: 300px;
}
.shoppingcart {
  width: 18px;
  height: auto;
  transform: translateY(3px);
}
li a:hover {
  transition: 0.3s;
  color: #66cae2;
}
a:link,
a:visited {
  font: Tahoma, Helvetica, Arial, "宋体", sans-serif;
  font-weight: 300;
  font-size: 15px;
  transition: 0.3s;
  color: rgb(235, 235, 235);
  text-decoration: none;
}
</style>


