<template>
  <footer class="aliHomeFooter">
    <p>{{copyright}} {{title}}</p>
  </footer>
</template>

<script>
export default {
  name: "aliHomeFooter",
  data() {
    return {
      copyright: "©MMMIU 2019️"
    };
  },
  props: {
    title: {
      type: String
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.aliHomeFooter {
  min-width: 600px;
  background-color: lightblue;
  padding: 12px;
  color: antiquewhite;
  text-align: center;
}
</style>



