<template>
  <div id="aliHomeHeader">
    <div class="greyBackground">
      <transition name="fade">
        <div
          v-if="show"
          class="background"
          :style="{backgroundImage: 'url(' + imgSet[imgIndex] + ')'}"
        ></div>
      </transition>
    </div>
    <div class="upper">
      <upperHeaderUpper></upperHeaderUpper>
      <upperHeaderLower></upperHeaderLower>
    </div>
    <headerMain @bgimg="bgimg"></headerMain>
    <lowerHeader></lowerHeader>
  </div>
</template>

<script>
import lowerHeader from "@/headercomponents/lowerHeader";
import upperHeaderUpper from "@/headercomponents/upperHeaderBar-upper";
import upperHeaderLower from "@/headercomponents/upperHeaderBar-lower";
import headerMain from "@/headercomponents/headerMain";
import image1 from "../headercomponents/images/1.png";
import image2 from "../headercomponents/images/2.png";
import image3 from "../headercomponents/images/3.png";
import image4 from "../headercomponents/images/4.png";
import image5 from "../headercomponents/images/5.png";

export default {
  name: "aliHomeHeader",
  data() {
    return {
      imgIndex: 0,
      show: false,
      image1: image1,
      image2: image2,
      image3: image3,
      image4: image4,
      image5: image5,
      imgSet: [image1, image2, image3, image4, image5],
      imgUrl: 'url("../headercomponents/images/1.png")'
    };
  },
  props: {},
  methods: {
    bgimg: function(index) {
      this.imgIndex = index;
      this.show = false;
      this.titleIndex = index;
      setTimeout(() => {
        this.show = true;
      }, 100);
    }
  },
  mounted: function() {
    this.show = true;
  },
  components: {
    upperHeaderUpper,
    upperHeaderLower,
    lowerHeader,
    headerMain
  }
};
</script>

<style scoped>
#aliHomeHeader {
  width: 100%;
  height: 640px;
  height: 640px;
}
.upper {
  width: 100%;
  height: 90px;
  transition-duration: 1s;
  background-color: none;
  border-bottom: 1px solid #292a2e;
}
.upper:hover {
  background-color: #1d2226;
  transition-duration: 1s;
  border: none;
}
.background {
  position: absolute;
  width: 100%;
  height: 640px;
  z-index: -101;
  background-size: 2100px;
  background-repeat: no-repeat;
  background-position: center;
}
.greyBackground {
  width: 100%;
  height: 640px;
  position: absolute;
  background-color: rgb(50, 50, 50);
  z-index: -9999;
}
.fade-enter-active {
  transition: all 0.4s cubic-bezier(0.215, 0.610, 0.355, 1);
}
.fade-leave-active {
  transition: 0s;
}
.fade-enter,
.fade-leave-to {
  transform: translateY(30px);
  opacity: 0;
}
</style>


